.tb_onsite_upload_btn_wrap{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center;
    padding: 10px;
}

.tb_snap_btn_wall.tb_onsite_upload_btn_wrap{
    position: fixed;
    z-index: 99999999;
    border-radius: 10px;
    overflow: hidden;
}

.tb_snap_btn_wall.tb_pos-1{
    bottom: 0px;
    right: 0px;
}
.tb_snap_btn_wall.tb_pos-2{
    bottom: 0px;
    left: 0px;
}
.tb_snap_btn_wall.tb_pos-3{
    top: 0px;
    right: 0px;
}
.tb_snap_btn_wall.tb_pos-4{
    top: 0px;
    left: 0px;
}

.tb_snap_btn_wall:not(.tb_snap_btn_wall_v).tb_pos-1 .tb_onsite_qr_btn_wrap,
.tb_snap_btn_wall:not(.tb_snap_btn_wall_v).tb_pos-3 .tb_onsite_qr_btn_wrap{
    flex-direction: row-reverse;
}


.tb_snap_btn_wall:not(.tb_snap_btn_wall_v).tb_pos-1 .tb_onsite_qr_code + .tb_onsite_btn_txt,
.tb_snap_btn_wall:not(.tb_snap_btn_wall_v).tb_pos-3 .tb_onsite_qr_code + .tb_onsite_btn_txt{
    text-align: right;
}

.tb_snap_btn_wall_v .tb_onsite_qr_btn_wrap{
    flex-direction: column;
    max-width: 130px;
}

.tb_snap_btn_wall_v .tb_onsite_qr_code:after {
    border-left: 10px solid #0000;
    border-right: 10px solid #0000;
    border-top: 10px solid #fff;
    bottom: -9px;
    content: "";
    display: block;
    height: 0;
    position: absolute;
    width: 0;
}

.tb_snap_btn_wall_v .tb_onsite_qr_code svg{
    height: 110px;
    width: 110px;
}

.tb_snap_btn_wall:not(.tb_snap_btn_wall_v) .tb_onsite_qr_code svg{
    height: 90px;
    width: 90px;
}

.tb_snap_btn_wall .tb_onsite_qr_code{
    padding: 10px;
    border-radius: 6px;
}

.tb_snap_btn_wall_v .tb_onsite_qr_code{
    margin-bottom: 12px;
}


.tb_snap_btn_wall .tb_onsite_btn{
    align-items: center;
}

.tb_snap_btn_wall .tb_onsite_upload_btn{
    max-width: 380px;
}

.tb_snap_btn_wall_v .tb_onsite_qr_code + .tb_onsite_btn_txt{
    text-align: center;
    max-width: 130px;
    justify-content: center;
}


.tb_snap_btn_bg {
    background-color: var(--tb-su-btn-background);
    height: 100%;
    left: 0;
    opacity: 1;
    position: absolute;
    top: 0;
    width: 100%;
}

.tb_onsite_upload_btn{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 350px;
    position: relative;
    z-index: 1;
    padding: 6px;
    border-radius: 3px;
    overflow: hidden;
    margin: 0 auto;
}

.tb_snap_btn_wall_v .tb_onsite_upload_btn{
    padding: 15px;
}

.tb_onsite_btn{
    display: flex;
    align-items: stretch;
    justify-content: center;
    cursor: pointer;
    width: 100%;
    overflow: hidden;
}
.tb_onsite_qr_btn_wrap{
    display: flex;
    align-items: stretch;
    justify-content: center;
    width: 100%;
}

.tb_onsite_qr_code{
    object-fit: cover;
    background: #fff;
    color:  var(--tb-su-btn-color);
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.tb_onsite_upload_btn .btn-scan canvas{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.tb_onsite_btn_txt{
    padding:0 10px;
    display: flex;
    align-items: center;
    flex-grow: 1;
    line-height: 1.5;
    position: relative;
    z-index: 1;
    font-size: var(--tb-su-btn-size);
    font-family: var(--tb-su-btn-font);
    font-variant: var(--tb-su-btn-varient);
    font-weight: var(--tb-su-btn-weight);
    color:  var(--tb-su-btn-color);
}

.tb_onsite_qr_code + .tb_onsite_btn_txt{
    text-align: left;
}

.tb_onsite_post__ .tb_onsite_upload_btn_wrap{
    padding: 0;
    padding-bottom: 100%;
    position: relative;
}
.tb_onsite_post__ .tb_onsite_upload_btn{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
}

.tb_onsite_post__ .tb_onsite_btn{
    height: 100%;
    width: 100%;
}
.tb_onsite_post__ .tb_onsite_qr_btn_wrap{
    flex-direction: column;
    width: 100%;
}

.tb_onsite_post__ .tb_onsite_qr_btn_wrap div:last-child{
    margin-bottom: 0;
}

.tb_onsite_post__  .tb_onsite_qr_code{
    background-color: transparent;
    margin-bottom: 8px;
}

.tb_onsite_post__ .tb_onsite_qr_code svg {
    width: 100%;
    height: 100%;
    max-width: 50%;
}

.tb_onsite_post__ .tb_onsite_btn_txt{
    padding: 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;  
    overflow: hidden;
}

.tb_onsite_post__ .tb_onsite_qr_code + .tb_onsite_btn_txt{
    text-align: center;
    -webkit-line-clamp: 2;
}

@media (max-width: 991px) {
    .tb_snap_btn_wall_v .tb_onsite_qr_btn_wrap {
        max-width: 110px;
    }
    .tb_snap_btn_wall_v .tb_onsite_qr_code svg {
        height: 90px;
        width: 90px;
    }
    .tb_snap_btn_wall_v .tb_onsite_upload_btn {
        padding: 10px;
    }
}